.bv-demo-page .home-title {
  display: inline-block;
  text-align: center;
  font-size: 0.533333rem;
  margin: 0.4rem 0;
  padding: 0 0.266667rem;
}

.bv-demo-page .bv-components-list {
  -webkit-tap-highlight-color: transparent;
}

.bv-demo-page .bv-components-item {
  height: 1.333333rem;
  margin: 0 0.2rem 0.266667rem;
  border-radius: 0.066667rem;
  background: #fff;
  -webkit-box-shadow: 0 0.066667rem 0.2rem 0 rgba(238, 243, 255, .5);
  box-shadow: 0 0.066667rem 0.2rem 0 rgba(238, 243, 255, .5);
  border: 0.013333rem solid black;
}


